@import url('https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Yeon+Sung&display=swap');

.App{
  font-family: 'Nanum Pen Script', cursive;
  font-family: 'Yeon Sung', cursive;
}

.main-bg{
  height: 500px;
  background-image: url('https://img.seoul.co.kr//img/upload/2021/07/20/SSI_20210720163116.jpg');
  background-size: cover;
  background-position: center;
  opacity: 0.78;
}

.pandaBtn{
  cursor: pointer;
}

.Plist{
  margin-top: 20px;
}

.addBao{
  text-align: right;
}

.AddPanda, .detail, .EditPanda{
  width : 40%;
  text-align : center;
  margin : 0 auto;
  background-color: #ececec;
  border: 1px solid gray;
  padding: 30px;
  border-radius: 40px;
  margin-top: 50px;
}

.detail {
  font-size: 30px;
}

.pandaInfo {
  margin-top: 20px;
}

.pandaImg{
  cursor: pointer;
}


